*
{
    padding: 0px;
    margin:10px;
    background-color: whitesmoke;
}

.h1-style
{
    color:blue;
    background-color: black;
    padding: 7px;
    text-align: center;
    border-radius: 30px;
    font-size: 60px;
    font-variant: small-caps;
    /* text-transform: capitalize; */
    text-shadow: 4px 4px 6px white;
    transition-property: all;
}
.h1-style:hover
{  
    transition-duration: 2s;
    transition-timing-function: ease-in-out;
    /* transition-delay: 1s; */
    color:black;
    background-color: red;
    padding: 7px;
    border-radius: 30px 50px;
    font-size: 60px;
    font-variant: small-caps;
    /* text-transform: capitalize; */
    text-shadow: 4px 4px 6px white;
    /* font-weight: bolder; */
}
.textarea-style
{
    margin-left: 12px;
    text-indent: 20px;
    text-transform: capitalize;
    font-size: 16px;
    color: red;
    background-color:#000000;
    border-radius: 30px;
    padding-top: 8px;
    transition-property: all;
}
.textarea-style:hover
{
    transition-duration: 1s;
    transition-timing-function: ease-in-out;
    background-color: red;
    color:black;
}
.upperCase-style
{
   padding: 7px;
   border-radius: 7px;
   cursor: pointer;
   margin:0px 20px;
}
.lowerCase-style
{
    padding: 7px;
   border-radius: 7px;
   cursor: pointer;
}
.upperCase-style:hover
{
    color:red;
    background-color: black;

}
.lowerCase-style:hover
{
    color:red;
    background-color: black;
}